<template>
  <img :src="image" class="nether" :data-row="row" :data-col="col" :style="{height: size + 'px', width: size + 'px'}">
</template>

<script>
  import NetherField from '../nether-field.js'
  
  export default {
    props: {
      solution: {
        type: NetherField,
        required: true
      },
      row: {
        type: Number,
        required: true
      },
      col: {
        type: Number,
        required: true
      },
      size: {
        type: Number,
        default: 32
      }
    },
    
    data () {
      return {
      }
    },
    
    computed: {
      image () {
        return 'images/' + this.solution.imageAt(this.row, this.col);
      }
    }
  }
</script>
  
<style scoped>
  img.nether {
    margin: 0;
    padding: 0;
    float: left;
  }
</style>


