export default class Solution {
  constructor(options={}) {
    this.options = options;
    
    this.mutationRate = parseFloat(options.mutationRate) || 0.05;
    this.crossoverRate = parseFloat(options.crossoverRate) || 0.1;

    this.pattern = options.pattern || this.randomPattern();
  }
  
  static size() { return 128; }

  static merge(dad, mom) {
    var currentParent = dad;
    var childPattern = [];
    
    for (var i = 0; i < this.size(); i++) {
      var bit = currentParent.pattern[i];
      
      // randomly mutate by flipping the bit
      if (Math.random() < currentParent.mutationRate) {
        bit ^= 1;
      }
      
      childPattern.push(bit);
      
      // randomly switch parents (crossover)
      if (Math.random() < currentParent.crossoverRate) {
        currentParent = (currentParent == dad) ? mom : dad;
      }
    }
    
    var newOptions = Object.assign({}, dad.options, {pattern: childPattern});
    var child = new this(newOptions);
    
    if (child.pattern != childPattern) {
      console.log('PATTERN MISMATCH!!!');
    }
    
    return child;
  }
  
  static mutate(pattern, mutationRate) {
    var newPattern = [];
    
    for (var i = 0; i < this.size(); i++) {
      var bit = pattern[i];
      
      if (Math.random() < mutationRate) {
        bit ^= 1;
      }
      
      newPattern.push(bit);
    }
    
    return newPattern
  }
  
  static hillClimb(solution, maxLevels=1) {
    var maxFitnessCandidate = solution;
    
    for (var level = 0; level < maxLevels; level++) {
      var candidates = [];
      var lastFitnessCandidate = maxFitnessCandidate;

      for (var i in lastFitnessCandidate.pattern) {
        var newPattern = [...lastFitnessCandidate.pattern];
      
        // flip bit "i"
        newPattern[i] ^= 1;
      
        candidates.push(new this({pattern: newPattern}));
      }
      // console.log('candidate size:', candidates.length);
      for (var candidate of candidates) {
        // console.log('candidate', candidate.fingerprint(), ', fitness', candidate.fitness);
        if (candidate.fitness > maxFitnessCandidate.fitness) {
          maxFitnessCandidate = candidate;
        }
      }

      // console.log(
      //   'last candidate:', lastFitnessCandidate.shorty(), '(', lastFitnessCandidate.fitness, ')',
      //   'max candidate:',   maxFitnessCandidate.shorty(), '(',  maxFitnessCandidate.fitness, ')'
      // );
      
      // if no improvement from last level, return. 
      if (lastFitnessCandidate == maxFitnessCandidate) {
        return maxFitnessCandidate;
      }
    }
    
    return maxFitnessCandidate;
  }
  
  calculateFitness() {
    return this.pattern.filter((x) => x == 1).length;
  }
  
  fingerprint() {
    var string = '';
    var nibbleSize = 4;
    
    for (var i = 0; i < this.constructor.size(); i+=nibbleSize) {
      var nibble = 0;
      
      for(var value of this.pattern.slice(i, i+nibbleSize)) {
        nibble <<= 1;
        nibble += value;
      }
      
      string += (nibble).toString(16);
    }
    
    return string;
  }
  
  shorty() {
    return this.fingerprint().slice(0,8);
  }
  
  // private
  
  randomPattern() {
    var pattern = [];
    
    for (var i = 0; i < this.constructor.size(); i++) {
      pattern.push(this.randomInt(2));
    }
    
    return pattern;
  }
  
  randomInt(size) {
    return Math.floor(Math.random() * size);
  }
}


