import Population from './population.js'

export default class Ga {
  constructor(options={}) {
    this.options = options;
    this.history = [];
    this.bests = [];
    this.memoizedGenerationFor = {};
    
    // defaults
    this.populationOptions = options.population || {size: 50, klass: Solution};
    this.generations = parseInt(options.generations) || 100;
    this.update = options.update;

    this.addPopulation();
  }

  addPopulation() {
    if (this.history.length == 0) {
      this.population = new Population(this.populationOptions);
    } else {
      this.population = this.population.newPopulation();
    }
    
    // save if this is a new best
    if (this.bests.length == 0 || this.population.best[0].fitness > this.bests[this.bests.length - 1].fitness) {
      this.bests.push(this.population.best[0]);
      
      if (this.update !== undefined) {
        this.update(this.bests[this.bests.length -1]);
      }
    }

    this.history.push(this.population.best[0]);
    
    return this.population;
  }
  
  run() {
    while (this.history.length < this.generations) {
      this.addPopulation();
    }
  }
  
  generate () {
    this.addPopulation();
  }
  
  best() {
    if (this.bests.length == 0) {
      return null;
    }
    
    return this.bests[this.bests.length - 1];
  }
  
  generationFor(fitness) {
    if (this.memoizedGenerationFor[fitness] !== undefined) {
      return this.memoizedGenerationFor[fitness];
    }
    
    for (var generation = 0; generation < this.history.length; generation++) {
      if (this.history[generation].fitness >= fitness) {
        this.memoizedGenerationFor[fitness] = generation;
        return generation;
      }
    }
    
    return 0;
  }
}


