import Solution from './solution.js'

export default class NetherField extends Solution {
  constructor(options={}) {
    super(options);
    
    this.cols = options.cols || Math.floor(Math.sqrt(this.constructor.size()));
    this.rows = options.rows || Math.floor(this.constructor.size() / this.cols);

    this.recurseCount = 0;
    this.alreadyTried = [];
    
    this.cellsWithPathsHome = this.calculateCellsWithPathsHome();
    this.fitness = this.calculateFitness();
  }
  
  static size() { return 256; }
  
  static indexToCoords(index) {
    if (this.memoizedIndexToCoords === undefined) {
      var side = Math.floor(Math.sqrt(this.size()));
      this.memoizedIndexToCoords = [];
      
      for (var i = 0; i < this.size(); i++) {
        var row = Math.floor(i / side);
        var col = i % side;
        
        this.memoizedIndexToCoords.push([row, col])
      }
    }
    
    return this.memoizedIndexToCoords[index];
  }
  
  static coordsToIndex(row, col) {
    if (this.memoizedCoordsToIndex === undefined) {
      var side = Math.floor(Math.sqrt(this.size()));
      this.memoizedCoordsToIndex = []
      
      for (var r = 0; r < side; r++) {
        this.memoizedCoordsToIndex.push([]);
        
        for (var c = 0; c < side; c++) {
          this.memoizedCoordsToIndex[r].push(r * side + c);
        }
      }
    }

    return this.memoizedCoordsToIndex[row][col]
  }
  
  calculateFitness() {
    var score = 0.0
    
    for (var index in this.pattern) {
      if (this.plantAt(index)) {
        score++;
      }
    }
    
    return score;
  }
  
  // private
  
  cell(index) {
    return this.pattern[index];
  }
  
  imageAt(row, col) {
    var index = this.coordsToIndex(row, col);
    
    if (this.homeAt(index)) {
      return 'home.png';
    } else if (this.cell(index)) {
      if (this.plantAt(index)) {
        return 'nether-wart.png';
      } else {
        return 'soul-sand.png';
      }
    }
    
    return 'acacia-block.png';
  }
  
  plantAt(index) {
    // has to be plantable, not empty
    if (this.emptyAt(index)) {
      return false;
    }
    
    // can't be "home cell"
    if (this.homeAt(index)) {
      return false;
    }
    
    // are there any adjacent empty cells which lead to the home cell?
    return this.pathToHomeAt(index);
  }
  
  emptyAt(index) {
    if (this.outOfBoundsAtIndex(index)) {
      return false;
    }
    
    return !this.cell(index);
  }
  
  homeAt(index) {
    return (index == 0);
  }
  
  pathToHomeAt(index) {
    return this.adjacentCells(index).filter((i) => this.cellsWithPathsHome.includes(i)).length > 0
  }
  
  calculateCellsWithPathsHome() {
    if (!this.emptyAt(0)) {
      return [];
    }
    
    return this.attachedEmptyCells(0);
  }
  
  attachedEmptyCells(index) {
    this.recurseCount++;
    this.alreadyTried = this.addIfUnique(this.alreadyTried, index);

    var someCells = this.arrayMinus(this.touchingCells(index), this.alreadyTried);
    var emptyCells = someCells.filter((x) => this.emptyAt(x));
    var newCells = [];
    
    for (var index of emptyCells) {
      if (!this.alreadyTried.includes(index)) {
        newCells = newCells.concat(this.attachedEmptyCells(index))
      }
    }
    
    return this.arrayUnique(this.alreadyTried.concat(newCells))
  }

  addIfUnique(list, element) {
    var newList = [...list];
    
    if (!list.includes(element)) {
      newList.push(element);
    }
    
    return newList;
  }
  
  arrayMinus(list, minus) {
    return list.filter((x) => !minus.includes(x))
  }
  
  arrayUnique(list) {
    var newList = [];
    
    for (var element of list) {
      if (!newList.includes(element)) {
        newList.push(element);
      }
    }
    
    return newList;
  }
  
  // adjacent in any of the 8 directions, ordinal+diagonal
  adjacentCells(index) {
    var coords = this.indexToCoords(index);
    var row = coords[0];
    var col = coords[1];
    
    return [
      this.mapToIndex(row-1, col-1),
      this.mapToIndex(row-1, col),
      this.mapToIndex(row-1, col+1),
      
      this.mapToIndex(row, col-1),
      this.mapToIndex(row, col+1),
      
      this.mapToIndex(row+1, col-1),
      this.mapToIndex(row+1, col),
      this.mapToIndex(row+1, col+1)
    ].filter((x) => x !== null);
  }
  
  // adjacent in only the 4 ordinal directions
  touchingCells(index) {
    var coords = this.indexToCoords(index);
    var row = coords[0];
    var col = coords[1];
    
    return [
      this.mapToIndex(row-1, col),
      this.mapToIndex(row+1, col),
      this.mapToIndex(row, col-1),
      this.mapToIndex(row, col+1),
    ].filter((x) => x !== null);
  }
  
  mapToIndex(row, col) {
    if (this.outOfBoundsAtCoords(row, col)) {
      return null;
    }
    
    return this.coordsToIndex(row, col);
  }
  
  outOfBoundsAtIndex(index) {
    var coords = this.indexToCoords(index);
    return this.outOfBoundsAtCoords(...coords);
  }
  
  outOfBoundsAtCoords(row, col) {
    return row < 0 || row >= this.rows || col < 0 || col >= this.cols;
  }
  
  indexToCoords(index) {
    var row = Math.floor(index / this.cols);
    var col = index % this.cols;
    
    return [row, col];
  }
  
  coordsToIndex(row, col) {
    return row * this.cols + col;
  }
}


