<template>
  <div class="cell-grid" :style="gridStyle" style="position: relative;">
    <div class="stats">
      F{{ solution.fitness }}
      <span v-if="generation > 0">G{{ generation }}</span>
    </div>
    <div v-for="row in solution.rows" :style="{height: size + 'px'}">
      <Cell v-for="col in solution.cols" :solution="solution" :row="row-1" :col="col-1" :size="size" />
      <br>
    </div>
  </div>
</template>

<script>
  import NetherField from '../nether-field.js';
  import Cell from './Cell.vue';
  
  export default {
    components: { Cell },
    props: {
      solution: {
        type: NetherField,
        required: true
      },
      generation: {
        type: Number,
        default: 0
      },
      size: {
        type: Number,
        default: 32
      }
    },
    
    data () {
      return {
      }
    },
    
    methods: {
      keyFor (row, col) {
        return 'cell-' + row + '-' + col;
      }
    },
    
    computed: {
      gridStyle () {
        // number of cols times col width, plus 2px border on each side
        return 'width: ' + ((this.solution.cols * this.size) + 4) + 'px;';
      }
    }
  }
</script>

<style scoped>
  .cell-grid {
    border: 2px solid #666;
  }
  
  .stats {
    position: absolute; 
    bottom: 0px; 
    right: 0px; 
    z-index: 100; 
    background-color: white; 
    font-weight: bold; 
    display: inline; 
    opacity: 0.5; 
    text-align: right; 
    padding-left: 5px; 
    padding-right: 5px;
  }
</style>


