export default class Population {
  constructor(options={}) {
    this.options = options;
    
    // if size is more than maxSize, only keep maxSize individuals in memory at a time,
    // deleting the lowest performer to make room for a new individual.
    this.maxSize = parseInt(options.maxSize) || 1000;
    
    // for efficiency's sake, we don't want to cull the population EVERY time it
    // goes over max size, so we allow a buffer.
    this.maxSizeBuffer = parseInt(options.maxSizeBuffer) || 500;
    
    this.maxSizeWithBuffer = this.maxSize + this.maxSizeBuffer;

    if (options.individuals) {
      // individuals per generation
      this.size = parseInt(options.size) || options.individuals.length;
      
      // class that defines the individual
      this.klass = options.individuals[0].constructor;
      
      // optionally pass in a collection of pre-existing individuals
      this.individuals = options.individuals;
    } else {
      this.size = parseInt(options.size) || 50;
      this.klass = options.klass || Solution;
      
      var startingSize = this.size < this.maxSize ? this.size : this.maxSize
      this.individuals = this.generateIndividuals(startingSize);
    }
    
    // save this many of the best (unique) individuals from the previous generation
    this.saveBest = parseInt(options.saveBest) || Math.ceil(this.size / 10);
    
    // create this many brand new, random individuals each generation
    this.createRandom = parseInt(options.createRandom) || Math.ceil(this.size / 10);
    
    // buffer each item's fitness by this percent before random weighted selection.
    // this gives the weaker options a chance to reproduce. A buffer of 0.1 will give
    // a solution of 0.1 a buffered fitness of 0.2, and a solution of 0.5 a buffered
    // fitness of 0.6. This keeps the fitter solutions from crowding out the others too
    // early, in order to aid diversity.
    this.fitnessBuffer = parseFloat(options.fitnessBuffer) || 0.1;
    
    // How many levels of steepest-ascent hill climbing should be applied to each
    // new solution. This is VERY computationally expensive. A 256-bit solution like
    // a NetherField will require (256 * hillClimbLevels + 1) times as much
    // computation.
    this.hillClimbLevels = parseInt(options.hillClimbLevels) || 0;
    
    // this is how we determine fitness based on percentage of potential fitness.
    this.maxFitness = parseFloat(options.maxFitness) || 256;
    
    // log extra output
    this.verbose = options.verbose || false
    
    this.solutionOptions = options.solution || {};
    
    this.best = this.calculateBest();
    console.log("population size:", this.best.length);
    
    this.log("fitness: " + this.best[0].fitness);
  }
  
  log(message) {
    if (this.verbose) {
      console.log(message);
    }
  }
  
  newPopulation(count) {
    count = count || this.size;
    
    // copy the best X individuals
    var individuals = this.best.slice(0,this.saveBest);
    
    // add random individuals
    for (var i = 0; i < this.createRandom; i++) {
      var rando = new this.klass(this.solutionOptions)
      individuals.push(this.hillClimb(rando, this.hillClimbLevels * 2));
    }
    
    var children = [];
    var childrenSize = count - individuals.length;

    // only keep the best, if maxSize is exceeded
    for (var i = 0; i < childrenSize; i++) {
      var s1 = this.selectIndividual();
      var s2 = this.selectIndividual();

      var merged = this.hillClimb(this.klass.merge(s1, s2));
      
      children.push(merged);

      if ((children.length + individuals.length) >= this.maxSizeWithBuffer) {
        children.sort((a,b) => b.fitness - a.fitness);
        children = children.slice(0, this.maxSize - individuals.length);
      }
    }
    
    // just in case we have extra children in the buffer
    children.sort((a,b) => b.fitness - a.fitness);
    children = children.slice(0, this.maxSize - individuals.length);
    
    var newOptions = Object.assign({}, this.options, {individuals: individuals.concat(children)})
    return new this.constructor(newOptions);
  }
  
  hillClimb(solution, levels) {
    if (levels === undefined) {
      levels = this.hillClimbLevels;
    }
    
    solution.pattern = this.klass.hillClimb(solution, levels).pattern;
    return solution;
  }
  
  calculateBest() {
    return this.individuals.sort((a,b) => b.fitness - a.fitness);
  }
  
  averageFitness() {
    return this.totalFitness() / this.size;
  }
  
  totalFitness() {
    return this.individuals.map((x) => x.fitness).reduce((sum, newer) => sum + newer);
  }
  
  totalBufferedFitness() {
    return this.individuals.map((x) => this.bufferedFitness(x.fitness)).reduce((sum, newer) => sum + newer)
  }
  
  bufferedFitness(fitness) {
    return (fitness / this.maxFitness) + this.fitnessBuffer;
  }
  
  // private
  
  selectIndividual() {
    var totalFitness = this.totalBufferedFitness();
    
    var arrow = Math.random() * totalFitness;
    
    for (var individual of this.best) {
      arrow -= this.bufferedFitness(individual.fitness);
      
      if (arrow <= 0) {
        return individual;
      }
    }
  }
  
  generateIndividuals(count) {
    var individuals = [];
    
    for (var i = 0; i < count; i++) {
      var individual = new this.klass(this.solutionOptions);
      this.hillClimb(individual, this.hillClimbLevels * 2)
      
      individuals.push(individual);
    }
    
    return individuals;
  }
}


